<template>
  <div id='map-key'>
    <div class='map-key-years'>
      <div class='map-key-year' v-for='i in 3' :class='{current: i === yearIndex}' @click='yearIndex = i'>
        <div class='map-key-checkbox'></div>
        <div class='map-key-period'  v-html='period(i)'></div>
      </div>
    </div>

  </div>
</template>

<script>
  export default {

    name: 'Mapkey',
    props: ['city', 'map'],
    data () {
      return {
        yearIndex: 1
      }
    },
    methods: {
      period (i) {
        return this.city.City['t' + i].substr(0, 4)
      }
    }
  }
</script>

<style lang="scss" scoped>
@import '../assets/colors.scss';

#map-key {
  position:fixed;
  top:101px;
  right:16px;
  width:190px;
  padding: 14px;
  background-color: white;
  color: $dark-grey-2;
  font-size: 12px;
  line-height:16px;

  .map-key-years {
    .map-key-year {
      margin:14px 0px;
      cursor: pointer;
      &:first-of-type{
        .map-key-checkbox {
          border: 1px solid #8747DF;
        }
        &.current {
          .map-key-checkbox {
            border: 0px solid #8747DF;
            background-color: #2E2E76;
          }
        }
      }
      &:nth-of-type(2) {
        .map-key-checkbox {
          border: 1px solid #AC2341;
        }
        &.current {
          .map-key-checkbox {
            background-color: #AC2341;
          }
        }
      }
      &:nth-of-type(3) {
        .map-key-checkbox {
          border: 1px solid #DA8B40;
        } 
        &.current {
          .map-key-checkbox {
            background-color: #DA8B40;
          }
        }
      }
      &.current {
        .map-key-checkbox {
          position:relative;
          &:after {
            content: '';
            background-image: url('/img/check.svg');
            position:absolute;
            left:0px;
            top:0px;
            width:100%;
            height:100%;
            background-size: 12px 10px;
            background-position: center center;
            display:block;
            background-repeat: no-repeat;
          }
        }
      }
      .map-key-period {
        display: inline-block;
      }
      .map-key-checkbox {
        margin-right: 8px;
        display: inline-block;
        width: 16px;
        height:16px;
        background: #FFFFFF;
        box-shadow: inset 0 1px 3px 0 rgba(0,0,0,0.50);
        border-radius: 2px;
      }
    }
  }
}
</style>



